<template>
    <div class="add-address">
        <header-com :title="'/'" ></header-com>
        <form id="form">
            <div class="weui-cells weui-cells_form">
            <div class="weui-cell">
                <div class="weui-cell__hd"><label class="weui-label"></label></div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="text" v-model="address.userName" placeholder="" required maxlength="10" tips=" 10" emptyTips="">
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd"><label class="weui-label"></label></div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="text" v-model="address.userPhone" pattern="REG_PHONE"  required placeholder="" emptyTips="" notMatchTips="" tips="">
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd"><label class="weui-label"></label></div>
                <div class="weui-cell__bd">
                    <input class="weui-input" type="text" v-model="address.address" placeholder="  /" required maxlength="30"  emptyTips="  /">
                </div>
                <div class="weui-cell__ft">
                    <i @click="selectMap" class="iconfont icon-dizhiguanli"></i>
                </div>
            </div>
            <div class="weui-cell">
                <div class="weui-cell__hd"><label class="weui-label"></label></div>
                <div class="weui-cell__bd">
                    <input class="weui-input" v-model="address.detailAddress" type="text" required placeholder=" " maxlength="50" emptyTips=" ">
                </div>
            </div>
            <div class="weui-cell weui-cell_switch">
                <div class="weui-cell__bd"></div>
                <div class="weui-cell__ft">
                    <input class="weui-switch" type="checkbox" v-model="address.ifDefault">
                </div>
            </div>
            <div class="weui-btn-area">
                <a v-if="ifAdd" class="weui-btn weui-btn_primary" href="javascript:" @click="addAddress"></a>
                <a class="weui-btn weui-btn_primary" href="javascript:" @click="editAddress" v-else></a>
            </div>
        </div>
        </form>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                address: {
                    ifDefault: false,
                    userPhone: '',
                    userName: '',
                    address: '',
                    detailAddress: '',
                },
                ifAdd:true
            }
        },
        methods: {
            getLocation: function () {
                if (navigator.geolocation) {
                    navigator.geolocation.getCurrentPosition(
                        function (position) {
                            //alert("Latitude: " + position.coords.latitude + "<br />Longitude: " + position.coords.longitude)

                        },
                        function (error) {
                            switch (error.code) {
                                case error.PERMISSION_DENIED:
                                    alert(".");
                                    break;
                                case error.POSITION_UNAVAILABLE:
                                    alert(".");
                                    break;
                                case error.TIMEOUT:
                                    alert(".");
                                    break;
                                case error.UNKNOWN_ERROR:
                                    alert(".");
                                    break;
                            }
                        }
                    );
                }
                else {
                    alert("")
                }
            },
            selectMap: function () {
                //  this.$router.push("/selectMap")
            },
            addAddress:function () {
                var self=this;
                weui.form.validate('#form', function (error) {
                    if (!error) {
                        self.$http.post("Address",self.address).then(r=>{
                            if(r.body.status)
                                weui.toast(r.body.message)
                            else
                                weui.topTips(r.body.message)
                        })
                    }
                    // return true; // return true
                }, {
                    regexp: {
                        PHONE: /^1[3|4|5|8][0-9]\d{4,8}$/,
                        VCODE: /^.{4}$/
                    }
                });

            },
            editAddress:function () {
                var self=this;
                self.$http.put("Address",self.address).then(r=>{
                    if(r.body.status)
                        weui.toast(r.body.message)
                    else
                        weui.topTips(r.body.message)
                })
            },
        },
        created: function () {
            //this.getLocation();
            if (this.$route.params&&this.$route.params.userPhone) this.ifAdd=false;
            this.address=this.$route.params;

        }
        ,
        mounted: function () {
            weui.form.checkIfBlur('#form', {
                regexp: {
                    PHONE: /^1[3|4|5|8][0-9]\d{8}$/,
                    VCODE: /^.{4}$/
                }
            });
        }
    }
</script>

<style>

</style>


